<template>
    <div>
        <div class="popup__header popup__header--dotted">
            <div class="title">Отказаться от купона</div>
            <div class="subtitle">{{ coupon.action.name}}</div>
        </div>
        
        
        <div class="popup__content">
            <div class="h2">Причина отказа</div>
            
            <div class="mt16">
                <textarea-field  placeholder="Например, «Продали квартиру, скидка больше не нужна...»"
                                 v-model="textCancel.value"
                                 :auto-focus="true"
                                 :is-error="textCancel.error"
                                 @focus="removeError(textCancel.name)"
                ></textarea-field>
            </div>
            
            
            <div class="row-b mt32">
                
                <div class="row-b__col elem-pos-fix mr24">
                    <button @click="sendForm()" class="btn btn--red">Отказаться</button>
                </div>
                <div class="row-b__col elem-pos-fit">
                    <div class="dif">
                        <span class="i i-warning i--red mr8"></span>
                        <div class="text-color-red">Не поленитесь подробно описать причину отказа от купона, <br>
                            иначе нам придётся вам позвонить и переспросить.</div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import TextareaField from '@/components/forms/TextareaField.vue'
    import {mapState} from 'vuex';

    export default {
        name: "CouponCancel",
        components: {
            TextareaField
        },
        props: {
            coupon: {
                default(){
                    return {}
                }
            }
        },
        data() {
            return {
                textCancel: {
                    name: 'textCancel',
                    value: '',
                    required: true,
                    error: false,
                    disabled: false,
                    placeholder: '',
                },
            }
        },
        mounted() {

        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        methods: {
            sendForm() {
                this.textCancel.error = false;
                if(this.textCancel.value.length < 10){
                    setTimeout(()=>{
                        this.textCancel.error = true;
                    },150);
                } else {
                    let params = {
                        text: this.textCancel.value,
                        refuse: true,
                    }
                    this.$store.dispatch('userCoupons/refuseCoupon', {
                        params: params,
                        popup: this.popups.couponCancel.name,
                        couponId: this.coupon.id,
                    });
                }
            },
            removeError (field) {
                this[field].error = false;
            },
        }
    }
</script>