<template>
    <div>
        <div class="popup__header popup__header--dotted">
            <div class="title">История купона</div>
            <div style="line-height: 29px;" class="subtitle mt16 mb16">{{ coupon.action.name }}</div>
            <div class="text-color-white-trans">{{ coupon.action.provider.name}}</div>
        </div>
        
        
        <div class="popup__content">
            <div class="coupon-history" style="height: 211px;">
                <div class="r">
                    <div class="r__l">
                        <div class="r__title">Оформлен</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r">{{ coupon.dateActive }}, в {{ coupon.timeActive }}</div>
                </div>

                <!--<div class="r">
                    <div class="r__l">
                        <div class="r__title">Город</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r">{{ coupon.city }}</div>
                </div>-->

                <div class="r">
                    <div class="r__l">
                        <div class="r__title">Владелец</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r">{{ coupon.user.fio }}</div>
                </div>

                <div class="r" v-if="coupon.status.code !== 'valid'">
                    <div class="r__l">
                        <div class="r__title" v-if="coupon.status.code === 'valid'">Активен</div>
                        <div class="r__title" v-if="coupon.status.code === 'extinguished'">Погашен</div>
                        <div class="r__title" v-if="coupon.status.code === 'expired'">Просрочен</div>
                        <div class="r__title" v-if="coupon.status.code === 'canceled'">Аннулирован</div>
                        <div class="r__title" v-if="coupon.status.code === 'withdrawn'">Отменён</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r">{{ coupon.status.date }}, в {{ coupon.status.time }}</div>
                </div>

                <div class="r" v-if="coupon.user.manager && coupon.status.code === 'extinguished'">
                    <div class="r__l">
                        <div class="r__title">Погасил</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r">{{ coupon.user.manager}}</div>
                </div>

                <div class="r" v-if="coupon.isCanceledIsModerator && coupon.status.code === 'canceled'">
                    <div class="r__l">
                        <div class="r__title">Аннулировал</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r">{{ coupon.user.moderator }} (модератор)</div>
                </div>

                <div class="r" v-if="!coupon.isCanceledIsModerator && coupon.status.code === 'canceled'">
                    <div class="r__l">
                        <div class="r__title">Аннулировал</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r">{{ coupon.user.manager }} (поставщик)</div>
                </div>

                <div class="r" v-if="coupon.price && coupon.status.code === 'extinguished'">
                    <div class="r__l">
                        <div class="r__title">Сумма покупки</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r text-bold">{{ coupon.priceFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></div>
                </div>

                <div class="r" v-if="coupon.discount.percent && coupon.status.code === 'extinguished'">
                    <div class="r__l">
                        <div class="r__title">Размер скидки</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r text-bold">{{ coupon.discount.percent }}%</div>
                </div>

                <div class="r" v-if="coupon.discount.sumFormat && coupon.status.code === 'extinguished'">
                    <div class="r__l">
                        <div class="r__title">Экономия</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r text-bold">{{ coupon.discount.sumFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></div>
                </div>

                <div class="r" v-if="coupon.comment && isPartner">
                    <div class="r__l">
                        <div class="r__title">Комментарий</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="r__r">{{ coupon.comment}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "CouponHistory",
        props: {
            coupon: {
                default: function () {
                    return {}
                }
            },
            isPartner: {
                default: false
            },
        },
        methods: {
        }
    }
</script>